<template>
    <div class="sport">
        <div class="box">
            <div class="header">简介</div>
            <div>
                <div>
                    {{ single.description }}
                </div>
                <el-link class="" type="success" style="float: right" :href="'https://baike.baidu.com/item/' + project">
                    ——来自百度百科
                    <i class="el-icon-link"></i>
                </el-link>
            </div>
        </div>
        <div class="box">
            <div class="header">体育活动注意</div>
            <div>
                {{ single.易受伤害 }}
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        props: ["project"],
        data() {
            return {
                dataset: {},
                single: {},
            };
        },
        async mounted() {
            this.dataset = await fetch("./51大项运动-人工补完.json", { cache: "force-cache" }).then((res) => res.json());
            this.single = this.dataset["田径"];
        },
        watch: {
            project(val) {
                this.single = this.dataset[val];
            },
        },
    };
</script>
<style lang="less" scoped>
    .sport {
        text-align: left;
        text-indent: 2em;
        display: flex;
        .box {
            flex: 1;
            padding: 20px;
        }
        .header {
            font-size: 20px;
            border-bottom: 1px rgb(79, 79, 79) solid;
            margin: 10px 0 10px 0;
        }
    }
</style>
