<template>
    <div>
        <div ref="svg" class="主页"></div>

        <el-dialog :title="project" :visible.sync="dialogVisible" width="50%" class="dialog">
            <my-dialog :project="project"></my-dialog>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import dialog from "@/views/体育项目馆";
    export default {
        components: {
            "my-dialog": dialog,
        },
        async mounted() {
            this.$refs.svg.innerHTML = await fetch("./体育项目.svg").then((res) => res.text());

            this.event();
            this.autoClick(this.$route.params.project);
        },
        data() {
            return {
                project: "田径",
                svg: "",
                dialogVisible: false,
            };
        },
        methods: {
            event() {
                let svg = this.$refs.svg;
                let $$ = (...arr) => {
                    return svg.querySelectorAll(arr);
                };

                let that = this;
                let clickEvent = function(e) {
                    that.dialogVisible = true;
                    that.project = this.id;
                    that.$router.replace(encodeURI("/体育项目馆/" + this.id));
                };
                $$("#图标 >g").forEach((i) => {
                    i.addEventListener("click", clickEvent);
                    let text = document.createElementNS("http://www.w3.org/2000/svg", "text");
                    text.innerHTML = i.id;
                    let { x, y, width, height } = i.getBBox();
                    Object.entries({
                        x: x + width / 2,
                        y: y + height,
                        style: "font-size: 7pt;",
                        "text-anchor": "middle",
                    }).forEach(([key, value]) => {
                        text.setAttribute(key, value);
                    });

                    i.append(text);
                });
            },
            autoClick(val) {
                this.project = val;
                if (this.dialogVisible != true) this.dialogVisible = true;
            },
        },
        watch: {
            "$route.params.project": function(val) {
                this.autoClick(val);
            },
        },
    };
</script>
<style lang="less">
    #图标 > g {
        transition: 0.45s;
        &:hover {
            transform: translateY(-10px);
        }
    }

    .el-dialog__title {
        font-size: 26px !important;
        font-weight: bold;
    }
    .主页 {
        text-align: center;
        width: 100vw;
        height: 100%;
        overflow: scroll;
        .selector {
            cursor: pointer;
        }
        svg {
            transform-origin: center center;
            overflow: visible;
            #图标 > g {
                cursor: pointer;
            }
            #太阳 {
                transform-origin: center center;
                animation: sunRotate 14s infinite;
            }
            [id^="椰树-"] {
                animation: treeShake 3s infinite alternate;
            }
            #金字塔,
            [id^="海星"],
            #椰子,
            #泳圈 {
                animation: float 3s infinite alternate;
                animation-delay: 1s;
            }
            #皮球,
            #泳圈 {
                animation: float 2s infinite alternate;
                animation-delay: 2s;
            }
            #西瓜,
            #冰棍 {
                animation: float 4s infinite alternate;
            }
            #雪 > circle:nth-child(3n + 1),
            #雪花,
            #雪花-3 {
                animation: disappear 2s infinite;
                animation-delay: 1s; /* W3C 和 Opera */
            }
            #雪 > circle:nth-child(3n + 2),
            #雪花-1,
            #雪花-4 {
                animation: disappear 3s infinite;
            }
            #雪 > circle:nth-child(3n + 3),
            #雪花-2,
            #雪花-5 {
                animation: disappear 1s infinite;
                animation-delay: 2s; /* W3C 和 Opera */
            }
            [id^="树"] {
                transform-origin: bottom right;
                animation: treeShake1 2s infinite alternate;
                animation-delay: 1s; /* W3C 和 Opera */
            }
            #雪人 {
                transform-origin: bottom left;
                animation: snowman 2s infinite alternate;
                animation-delay: 1s; /* W3C 和 Opera */
            }
            [id^="贝壳"] {
                animation: float 3s infinite alternate;
            }
        }
    }
    @keyframes sunRotate {
        0% {
            transform: rotate(-90deg);
        }
        50% {
            transform: rotate(0);
        }
        100% {
            transform: rotate(90deg);
        }
    }
    @keyframes treeShake {
        0% {
            transform: rotate(358deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
    @keyframes treeShake1 {
        0% {
            transform: rotate(359deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
    @keyframes float {
        50% {
            transform: translateY(10px);
        }
    }
    @keyframes disappear {
        0% {
            opacity: 0;
        }
        20% {
            opacity: 1;
        }
        100% {
            transform: translateY(80px);
            opacity: 0;
        }
    }
    @keyframes snowman {
        0% {
            transform: rotate(347deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
    .dialog {
        font-family: cursive;
    }
</style>
